* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: #4f46e5;
  color: white;
}

.btn-primary:hover {
  background-color: #4338ca;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.3);
}

.btn-secondary {
  background-color: #10b981;
  color: white;
}

.btn-secondary:hover {
  background-color: #059669;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.grid {
  display: grid;
  gap: 24px;
}

.grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.grid-2 {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}

.page-title {
  font-size: 2.5rem;
  text-align: center;
  color: #1f2937;
  margin-bottom: 3rem;
  font-weight: 700;
}

.section {
  padding: 4rem 0;
}

.header {
  width: 100%;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
  text-decoration: none;
}

.logo {
  width: 150px;
  height:150px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 18px;
}

.logo-text {
  font-size: 1.8rem;
  font-weight: bold;
  color: #4f46e5;
  text-decoration: none;
}

.nav {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.nav-link {
  color: #4b5563;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.nav-link:hover {
  color: #4f46e5;
  background-color: #f3f4f6;
}

.nav-link.active {
  color: #4f46e5;
  background-color: #eef2ff;
  font-weight: 600;
}

.hero {
  padding: 6rem 0 4rem;
  text-align: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.hero-title {
  font-size: 3rem;
  margin-bottom: 1rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 3rem;
  opacity: 0.9;
}

.search-box {
  max-width: 600px;
  margin: 0 auto;
}

.search-input {
  width: 100%;
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  outline: none;
}

.search-input:focus {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.feature-card {
  padding: 2.5rem 2rem;
  text-align: center;
  height: 100%;
  border: 1px solid #e5e7eb;
}

.feature-card h3 {
  font-size: 1.5rem;
  color: #1f2937;
  margin-bottom: 1rem;
  font-weight: 600;
}

.feature-card p {
  color: #6b7280;
  line-height: 1.6;
  font-size: 1.1rem;
}

.interview-card {
  padding: 3rem;
  text-align: center;
  border: 1px solid #e5e7eb;
}

.interview-card h2 {
  font-size: 2.5rem;
  color: #1f2937;
  margin-bottom: 2rem;
  font-weight: 700;
}

.interview-points {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.interview-point {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 1.2rem;
  color: #374151;
}

.check-icon {
  background-color: #10b981;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  flex-shrink: 0;
}

.profession-card {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #e5e7eb;
}

.profession-title {
  font-size: 1.5rem;
  color: #1f2937;
  margin-bottom: 1rem;
  font-weight: 600;
}

.profession-description {
  color: #6b7280;
  margin-bottom: 1.5rem;
  flex-grow: 1;
  line-height: 1.5;
}

.profession-details {
  margin-bottom: 1.5rem;
}

.detail-item {
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.detail-label {
  font-weight: 600;
  color: #374151;
  margin-right: 1rem;
  white-space: nowrap;
}

.detail-value {
  color: #6b7280;
  text-align: right;
  flex: 1;
}

.salary {
  color: #4f46e5;
  font-weight: 600;
  font-size: 1.1rem;
}

.demand {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  color: white;
}

.demand.high {
  background-color: #10b981;
}

.demand.medium {
  background-color: #f59e0b;
}

.demand.low {
  background-color: #ef4444;
}

.full-width {
  width: 90%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-bottom:20px;
}

.test-card {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #e5e7eb;
}

.test-title {
  font-size: 1.5rem;
  color: #1f2937;
  margin-bottom: 1rem;
  font-weight: 600;
}

.test-description {
  color: #6b7280;
  margin-bottom: 1.5rem;
  flex-grow: 1;
  line-height: 1.5;
}

.test-meta {
  margin-bottom: 1.5rem;
  background: #f8fafc;
  padding: 1rem;
  border-radius: 8px;
}

.meta-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.meta-item:last-child {
  margin-bottom: 0;
}

.meta-label {
  font-weight: 500;
  color: #4b5563;
}

.meta-value {
  color: #1f2937;
  font-weight: 600;
}

.education-content {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.map-section {
  padding: 2rem;
}

.map-section h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #1f2937;
  text-align: center;
}

.map-placeholder {
  height: 400px;
  background: linear-gradient(135deg, #e0f2fe 0%, #bae6fd 100%);
  border-radius: 12px;
  position: relative;
  border: 2px dashed #0ea5e9;
}

.map-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #0369a1;
  font-weight: 500;
}

.map-points {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.map-point {
  position: absolute;
  background: #ef4444;
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.map-point:hover {
  transform: scale(1.1);
  background: #dc2626;
}

.universities-list h2 {
  font-size: 1.8rem;
  margin-bottom: 2rem;
  color: #1f2937;
  text-align: center;
}

.university-card {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #e5e7eb;
}

.university-name {
  font-size: 1.4rem;
  color: #1f2937;
  margin-bottom: 1rem;
  font-weight: 600;
}

.programs-list h4 {
  font-size: 1.1rem;
  color: #374151;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.programs-list ul {
  list-style: none;
  margin-bottom: 1.5rem;
}

.programs-list li {
  padding: 0.5rem 0;
  color: #6b7280;
  border-bottom: 1px solid #f3f4f6;
}

.programs-list li:last-child {
  border-bottom: none;
}

.planning-steps {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.planning-step {
  padding: 2.5rem 2rem;
  border: 1px solid #e5e7eb;
  position: relative;
}

.step-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  gap: 1rem;
}

.step-number {
  background: #4f46e5;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.2rem;
  flex-shrink: 0;
}

.step-title {
  font-size: 1.5rem;
  color: #1f2937;
  font-weight: 600;
  margin: 0;
}

.step-description {
  color: #6b7280;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  line-height: 1.5;
}

.step-tasks {
  margin-bottom: 2rem;
}

.step-tasks h4 {
  font-size: 1.1rem;
  color: #374151;
  margin-bottom: 1rem;
  font-weight: 600;
}

.step-tasks ul {
  list-style: none;
  padding-left: 0;
}

.step-tasks li {
  padding: 0.5rem 0;
  color: #6b7280;
  position: relative;
  padding-left: 1.5rem;
}

.step-tasks li:before {
  content: '•';
  color: #4f46e5;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.consultants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.consultant-card {
  padding: 2rem;
  text-align: center;
  border: 1px solid #e5e7eb;
}

.consultant-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #4f46e5, #7c3aed);
  margin: 0 auto 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2rem;
  font-weight: bold;
}

.consultant-name {
  font-size: 1.3rem;
  color: #1f2937;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.consultant-specialization {
  color: #6b7280;
  margin-bottom: 1rem;
}

.consultation-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  border: 1px solid #e5e7eb;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #374151;
  margin-left: 34px;
}

.form-input,
.form-textarea,
.form-select {
  width: 90%;
  display: flex;
  margin: 0 auto;
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.about-text {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #6b7280;
  margin-bottom: 2rem;
}

.contact-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.contact-item {
  text-align: center;
  padding: 2rem;
  border: 1px solid #e5e7eb;
}

.contact-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #4f46e5;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    gap: 1rem;
  }
  
  .nav {
    justify-content: center;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .interview-point {
    font-size: 1rem;
  }
  
  .detail-item {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .detail-value {
    text-align: left;
  }
  
  .map-placeholder {
    height: 300px;
  }
  
  .step-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .planning-step {
    padding: 2rem 1.5rem;
  }
  
  .page-title {
    font-size: 2rem;
  }
}
