}

.program-category {
  margin-bottom: 2rem;
}

.category-title {
  font-size: 2rem;
  color: #1f2937;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  border-bottom: 3px solid #4f46e5;
