/* Стили для выпадающего меню */
/* Стили для выпадающего меню с анимацией */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-toggle {
  display: flex;
  align-items: center;
  gap: 5px;
  background: none;
  border: none;
  cursor: pointer;
  font-family: inherit;
  transition: all 0.3s ease;
}

.dropdown-arrow {
  font-size: 0.8rem;
  transition: transform 0.3s ease;
}

.dropdown.open .dropdown-arrow {
  transform: rotate(180deg);
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  min-width: 220px;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  border: 1px solid #e5e7eb;
  z-index: 1000;
  margin-top: 5px;
  overflow: hidden;
  animation: dropdownFadeIn 0.2s ease-out;
}

.dropdown-item {
  display: block;
  padding: 12px 16px;
  color: #4b5563;
  text-decoration: none;
  border-bottom: 1px solid #f3f4f6;
  transition: all 0.2s ease;
  font-weight: 500;
}

.dropdown-item:hover {
  background-color: #f8fafc;
  color: #4f46e5;
}

.dropdown-item:last-child {
  border-bottom: none;
}

/* Анимация появления */
@keyframes dropdownFadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Анимация исчезновения */
.dropdown-menu.closing {
  animation: dropdownFadeOut 0.3s ease-in;
}

@keyframes dropdownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-10px);
  }
}

/* Адаптивность для выпадающего меню */
@media (max-width: 768px) {
  .dropdown-menu {
    position: fixed;
    top: auto;
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
    max-width: 300px;
  }
}

/* Улучшенные стили для активного состояния */
.nav-link.active {
  color: #4f46e5;
