/* Стили для точной SVG карты Армении */
.armenia-map-container {
  position: relative;
  width: 100%;
  height: 600px;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  border: 2px solid #e2e8f0;
  margin-bottom: 2rem;
}

.armenia-map {
  width: 100%;
  height: 100%;
}

/* Базовая карта */
.base-map {
  filter: brightness(1.05) contrast(1.1);
}

/* Подсветка региона */
.region-highlight {
  pointer-events: none;
}

/* Названия регионов */
.regions-labels .region-label {
  font-size: 14px;
  fill: #374151;
  font-weight: 600;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px white;
}

.regions-labels .region-label:hover {
  fill: #4f46e5;
  font-size: 15px;
}

/* Университеты */
.university-point {
  cursor: pointer;
  transition: all 0.3s ease;
}

.university-glow {
  opacity: 0;
  transition: all 0.3s ease;
}

.university-point:hover .university-glow {
  opacity: 1;
  r: 12;
}

.university-dot {
  transition: all 0.3s ease;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.university-point:hover .university-dot {
  r: 6;
  stroke-width: 3;
  filter: drop-shadow(0 0 8px rgba(124, 58, 237, 0.6));
}

.university-label {
  font-size: 10px;
  fill: #1f2937;
  font-weight: 700;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  pointer-events: none;
  text-shadow: 1px 1px 3px white;
  transition: all 0.3s ease;
}

.university-point:hover .university-label {
  font-size: 11px;
  fill: #4f46e5;
}

/* Легенда на SVG */
.map-legend-svg .legend-title {
  font-size: 12px;
  fill: #1f2937;
  font-weight: 700;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.legend-item .legend-text {
  font-size: 10px;
  fill: #374151;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Легенда для мобильных */
.mobile-legend {
  display: none;
  background: rgba(255, 255, 255, 0.95);
  padding: 15px;
  border-radius: 8px;
  margin-top: 15px;
  border: 1px solid #e2e8f0;
}

.mobile-legend h4 {
  margin: 0 0 10px 0;
  color: #1f2937;
  font-size: 0.9rem;
  font-weight: 600;
}

/* Фильтр регионов */
.regions-filter {
  margin-bottom: 2rem;
  text-align: center;
}

.regions-filter h3 {
  margin-bottom: 1rem;
  color: #374151;
  font-size: 1.2rem;
}

.filter-tags {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.filter-tag {
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
