  font-size: 0.9rem;
}

.filter-tag:hover {
  background: #e5e7eb;
}

.filter-tag.active {
  background: #4f46e5;
  color: white;
  border-color: #4f46e5;
}

/* Бейджи университетов */
.university-badges {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.university-region {
  background: #dbeafe;
  color: #1e40af;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.7rem;
  font-weight: 600;
}

/* Адаптивность */
@media (max-width: 768px) {
  .armenia-map-container {
    height: 400px;
  }
  
  .map-legend-svg {
    display: none;
  }
  
  .mobile-legend {
    display: block;
  }
  
  .regions-labels .region-label {
    font-size: 10px;
  }
  
  .university-label {
    font-size: 8px;
  }
  
  .filter-tags {
    justify-content: flex-start;
  }
}

@media (max-width: 480px) {
