/* Основной фон подсказки с тенью */
.tooltip-background {
  filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.15));
  fill: #ffffff;
  stroke: #e5e7eb;
  stroke-width: 1.5;
}

