.tooltip-inner-background {
  fill: #ffffff;
  stroke: none;
}

.tooltip-text {
  font-size: 11px;
  fill: #1f2937;
  font-weight: 600;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  dominant-baseline: central;
  text-anchor: middle;
  pointer-events: none;
}

.tooltip-subtext {
  font-size: 10px;
  fill: #6b7280;
  font-weight: 500;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  dominant-baseline: central;
  text-anchor: middle;
  pointer-events: none;
}

/* Улучшенная версия с градиентным белым фоном */
.university-tooltip .enhanced-background {
  fill: white;
  fill-opacity: 0.98;
